.files{
  margin-top: 1rem;
  border: 1px solid #073453;
  border-radius: 1.5rem;
  height: auto;
  width: 100%;
  color: #073453;
  }

  .files h2{
    margin: 2rem 0 0 2rem;
    font-weight: 100;
    font-size: 20px;
  }

  .files .fas {
    color: #ee9796;
    text-align: center;
    font-size: 1.8rem;
    margin-left: 1rem;
    margin-right: 1rem;
  }
  
  #doc_list{
    width: 100%;
    border-spacing: 0 1rem;
    border-collapse: separate;
  }
  
  .fileDetails, .icon-item, .ajs-button{
    cursor: pointer;
  }

  .profile_picture{
    margin-top:1rem;
  }
  .details{
    height: 50px;
  }
  @media (min-width: 533px) and (max-width: 1023){
    .files{
      width: 102%;
    }
  }
 
  @media (min-width: 1024) and (max-width: 1279px){
    .files{
      width: 100%;
    }
  }