$font-color:#073453;      
$icon-color: #ee9796;

.TrashIcon{
    cursor: pointer;
    color:$icon-color;
    margin-left:0.5rem;
}

.UndoIcon{
    cursor: pointer;
    color:$icon-color;
    margin-left:0.5rem; 
}

td.file_name-u{
    word-break: break-all;
}

.deleted_t{
    padding-left:1rem;
}
th#action-trash {
    border-right: 1px solid #073453;
    border-top-right-radius: 1rem;
   border-bottom-right-radius: 1rem;

}

.btn-cancel, .btn-continue{
    cursor: pointer;
}

#label{
    cursor:pointer;
}
#option{
    cursor:pointer;
}
th#created{
    overflow:hidden;
}
th#deleted{
    overflow:hidden;
}
th#action-trash{
    padding-left:0rem;
}
.file_name_t{
    overflow:hidden;
}

@media (min-width:533px) and (max-width:784px){
    .filesUpload td{
        font-size: 14px;
    }
    th#created{
        padding-left:0.1rem;
        font-size: 14px;

    }
    #item_name{
        font-size: 14px;
    }
    th#deleted{
        padding-left:0rem;
        font-size: 14px;
     }
     .created_t{
        padding-left:0.1rem;
 
     }
     .deleted_t{
        padding-left:0.1rem;
     }
     th#action-trash{
        padding-left:0rem;
        font-size: 14px;
    }
    .file_name-u{
        padding-left:0rem;
        font-size: 14px;
    }
    .delete-icon{
        padding-left:0rem;
        font-size: 14px;
    }
    
}

@media(min-width:1024px){
    #icon01 input[type=checkbox] {
        margin-left:0.8rem;
      }
   .created_t{
       padding-left:1rem;
    }
   .deleted_t{
       padding-left:1rem;
   }
   #created{
       padding-right:1.3rem;
       padding-left:0.6rem;
       overflow:hidden;
       white-space: nowrap;
   }
   #deleted{
       padding-right:2rem;
       overflow:hidden;
       white-space: nowrap;
 }
   #action-trash{
       padding-right:0.5rem;
   }
}