#share-details-n {
    display: grid;
    grid-template-columns: 40% 60%;
    margin-top: 3rem;;
    margin-left: 3.4rem;
}

#column-1 p {
    font-size: 22px;
    color: #073453;
    font-family: 'Open Sans', sans-serif;
}

#share-details-n p {
    margin-top: 3.4rem;
    font-size: 18px !important;
}

#share-details-n input {
    padding: 0.8rem;
    margin-left: 2rem;
    margin-top: 2rem;
    width: 20rem;
    border: #E07050 3px solid;
    border-radius: 1rem;
}

.btn01 {
    cursor: pointer;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    margin: 5rem 0 0 0.8rem;
    padding: 1rem;
    width: 12rem;
    background-color: #E07050;
    color: white;
    font-size: 16px;
    border-radius: 1rem;
}

.btn02 {
    cursor: pointer;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    margin: 5rem 0 0 7rem;
    padding: 1rem;
    width: 12rem;
    background-color: #E07050;
    color: white;
    font-size: 16px;
    border-radius: 1rem;
}

@media (min-width:533px) and (max-width:1022px){
    #share-details-n p {
        margin-top: 3.4rem;
        font-size: 16px !important;
    }

    #share-details-n input {
        padding: 0.5rem;
        margin-left: 2rem;
        margin-top: 2rem;
        width: 15rem;
        border: #E07050 3px solid;
        border-radius: 1rem;
    }  

    .btn01 {
        margin: 4rem 0 0 0;
        width: 10rem;
    }
    
    .btn02 {
        margin: 4rem 0 0 5rem;
        width: 10rem;
    }
}
